
export interface OnSendEventFunc {
    (event: Office.AddinCommands.Event): Promise<boolean>
}
export interface DialogResult{
    message:boolean,
    error:Error
}

class OnSendApp {
    public mailboxItem: Office.Item & Office.ItemCompose & Office.ItemRead & Office.MessageRead & Office.MessageCompose & Office.AppointmentRead & Office.AppointmentCompose;
    public thisUser: string;
    public initlized:boolean=false;
    private onsendHooks: OnSendEventFunc[] = [];
    constructor() {
        this.getGlobal().OnSendEvent = this.onSendEvent;
        this.init();
    }
    init() {
        Office.onReady(() => {
            // init here
            this.mailboxItem = Office.context.mailbox.item;
            this.thisUser = Office.context.mailbox.userProfile.emailAddress;
            this.initlized=true;
    
        });
    }
    addEventHook(hook: OnSendEventFunc) {
        this.onsendHooks.push(hook);
    }
  
    getGlobal(): any {
        return typeof self !== "undefined"
            ? self
            : typeof window !== "undefined"
                ? window
                : typeof global !== "undefined"
                    ? global
                    : undefined;
    }
    /**
     * onsend エベント関数
     * @param event 
     */
    async onSendEvent(event: Office.AddinCommands.Event) {
        const app = onSendApp;
        const hookPromises=[];
        for(let hook of app.onsendHooks){
            hookPromises.push(hook(event));
        }
        Promise.all(hookPromises).then((results)=>{
            for(let result of results){
                if (!result) {
                    event.completed({ allowEvent: false });
                    return;
                }
            }
            event.completed({ allowEvent: true });
        });
    }
    /**
     * 
     * @param msg 
     */
    showInformation(msg: string) {
        this.mailboxItem.notificationMessages.addAsync("information", {
            type: "informationalMessage",
            message: msg,
            icon: "icon16",
            persistent: false
        });
    }
  

   /**
    * 
    * @param pageName 表示画面名
    * @param height 高さ
    * @param width 幅
    */
    async OpenDialogAsync(url:string,height:number,width:number):Promise<DialogResult> {
        let dlgResult :DialogResult={message:null,error:null};
        return new Promise((resolve)=>{
            Office.context.ui.displayDialogAsync(
                url,    //ダイアログ画面URL
                { height: height, width: width, displayInIframe: true },
                function (result) {
                    var dialog = result.value;
                    console.log(result);
                    if (dialog !== undefined) {
                        //ダイアログの戻り値待ち
                        dialog.addEventHandler(Office.EventType.DialogMessageReceived, function (args) {
                            console.log("DialogMessageReceived=>",args);
                            dialog.close();
                            dlgResult.message= JSON.parse(args.message);
                            resolve(dlgResult);
                        });
                        //異常終了待ち
                        dialog.addEventHandler(Office.EventType.DialogEventReceived, function handleAuthDialogMessage(args) {
                            console.log("DialogEventReceived=>",args);
                            dialog.close();
                            dlgResult.error=args.error;
                            resolve(dlgResult);
                        });
                    }
                }
            );
        });
    }

}
const onSendApp = new OnSendApp();
export { onSendApp as app };
