import { app } from "./onsend-app";
import OfficeUtil from "../common/officeUtil";

/**
 * 
 * @param event 
 */
function sendEventHook(event: Office.AddinCommands.Event):Promise<boolean>{
    console.log(event.source.id);
    let util = new OfficeUtil(Office.context.mailbox);
    return util.saveItemAsync().then(()=>{
      let url = window.location.origin + '/taskpane.html';
      return app.OpenDialogAsync(url, 70, 60).then((result)=>{
        if (result.error == null) {
          return result.message;
        } else {
          console.log(result.error)
          return false;
        };
      });
    });
}

app.addEventHook(sendEventHook);