export default class OfficeUtil {

    mailbox: Office.Mailbox;
    item: Office.Item & Office.ItemCompose & Office.ItemRead & Office.MessageRead & Office.MessageCompose & Office.AppointmentRead & Office.AppointmentCompose;

    constructor(mailbox: Office.Mailbox) {
        this.mailbox = mailbox;
        this.item = mailbox.item;
    }
    /**
     * メールを一時保存する
     * @returns itemIdを返す 
     */
    saveItem():Promise<string>{
        const item = Office.context.mailbox.item;
        return new Promise((resolve)=>{
            item.saveAsync((result:Office.AsyncResult<string>)=>{
                if(result.status===Office.AsyncResultStatus.Succeeded){
                    resolve(result.value);
                }else{
                    resolve(null);
                }
            });
        });
    }
    /**
       * メールアドレスリストを取得する。
       * @param recipients 受信者/作成モードのみ
       */
    getEmailsAsync(recipients: Office.Recipients): Promise<Office.EmailAddressDetails[]> {
        return new Promise((resolve, reject) => {
            recipients.getAsync((result: Office.AsyncResult<Office.EmailAddressDetails[]>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.info("getEmailsAsync", result.value);
                    resolve(result.value);
                } else {
                    console.error("getEmailsAsync Error:", result);
                    reject(result.error);
                }
            });
        })
    }
    /**
     * 件名を取得
     * @param subject 
     */
    getSubjectAsync(): Promise<string> {
        return new Office.Promise((resolve, reject) => {
            this.item.subject.getAsync((result: Office.AsyncResult<string>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.info("getSubjectAsync", result.value);
                    resolve(result.value);
                } else {
                    console.error("getSubjectAsync Error:", result);
                    reject(result.error);
                }
            });
        });
    }

    /**
     * ItemIdを取得
     */
    getItemIdAsync(): Promise<string> {
        return new Promise((resolve, reject) => {
            this.item.getItemIdAsync((result: Office.AsyncResult<string>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.info("getItemIdAsync", result.value);
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });
    }
    /**
     * UserIdentityTokenを取得（未使用）
     */
    getUserIdentityTokenAsync(): Promise<string> {
        return new Promise((resolve, reject) => {
            this.mailbox.getUserIdentityTokenAsync((result: Office.AsyncResult<string>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.warn("getUserIdentityTokenAsync", result.value);
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });
    }

    /**
     * AccessTokenを取得
     */
    getCallbackTokenAsync(): Promise<string> {
        return new Promise((resolve, reject) => {
            this.mailbox.getCallbackTokenAsync({ isRest: true }, (result: Office.AsyncResult<string>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.info("getCallbackTokenAsync", result.value);
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });
    }

    /**
     * 送信者を取得
     */
    getFromAsync(): Promise<Office.EmailAddressDetails> {
        return new Promise((resolve, reject) => {
            this.item.from.getAsync((result: Office.AsyncResult<Office.EmailAddressDetails>) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    console.info("getFromAsync", result.value);
                    resolve(result.value);
                } else {
                    console.error("getFromAsync Error:", result);
                    reject(result.error);
                }
            });
        });
    }

    /**
     * カスタマイズ属性取得する
     * @param propName 
     */
    getPropAsync(propName: string): Promise<any> {
        return new Promise((resolve) => {
            this.item.loadCustomPropertiesAsync((result: Office.AsyncResult<Office.CustomProperties>) => {
                var value = result.value.get(propName);
                if (result.status === Office.AsyncResultStatus.Succeeded && value) {
                    resolve(value);
                } else {
                    resolve(null);
                }
            });
        });
    }

    /**
     * メール内容を保存する
     */
    saveItemAsync() {
        let item = this.item;
        let rawId = this.saveItem();
        let to = this.getEmailsAsync(item.to);
        let cc = this.getEmailsAsync(item.cc);
        let bcc = this.getEmailsAsync(item.bcc);
        let from = this.getFromAsync();
        let subject = this.getSubjectAsync();
        // let rawId = this.getItemIdAsync();
        let callbackToken = this.getCallbackTokenAsync();
        return Promise.all([to, cc, bcc, from, subject, rawId, callbackToken])
            .then(([to, cc, bcc, from, subject, rawId, callbackToken]) => {
                const user = Office.context.mailbox.userProfile;
                const itemId = Office.context.mailbox.convertToRestId(rawId, "v2.0");
                const copyItem = { to, cc, bcc, from, subject, itemId, user };
                const restInfo: RestApiInfo = {
                    restUrl: Office.context.mailbox.restUrl,
                    callbackToken: callbackToken
                }
                console.info("copyItem=>", copyItem);
                console.info("RestApiInfo=>", restInfo);
                localStorage.removeItem("mailItem");
                localStorage.removeItem("RestInfo");
                localStorage.setItem("mailItem", JSON.stringify(copyItem));
                localStorage.setItem("RestInfo", JSON.stringify(restInfo));
            });
    }

}